import {useForm} from "react-hook-form";
import {joiResolver} from "@hookform/resolvers/joi";

import {createOrderValidator} from "../../../validators";
import {orderService} from "../../../services";
import {useState} from "react";
import {useNavigate} from "react-router-dom";

const CreateOrder = () => {
    const [fileList, setFileList] = useState([]);
    const [fileLimit, setFileLimit] = useState(false);
    const [hideButton, setHideButton] = useState(false);


    const navigate = useNavigate();

    const LIMIT_FILES_TO_UPLOAD = 5
    const formData = new FormData();

    const handleUploadFile = files => {
        const uploaded = [...fileList]
        let limitExceeded = false

        //////       ,     
        files.some((file) => {
            ////         
            if (uploaded.findIndex((f) => f.name === file.name) === -1) {
                uploaded.push(file)

                if (uploaded.length === LIMIT_FILES_TO_UPLOAD) {
                    setFileLimit(true);
                }
                if (uploaded.length > LIMIT_FILES_TO_UPLOAD) {
                    alert(`You can only add a maximum of ${LIMIT_FILES_TO_UPLOAD} files`);
                    setFileLimit(false);
                    limitExceeded = true;
                    return true;
                }
            }
        })

        if (!limitExceeded) setFileList(uploaded)

    }

    const handleFileEvent = (e) => {
        const selectedFiles = Array.prototype.slice.call(e.target.files)
        handleUploadFile(selectedFiles);
    };

    const {
        handleSubmit,
        register,
        reset,
        formState: {isValid, errors}
    } = useForm({resolver: joiResolver(createOrderValidator), mode: 'all'});


    const submit = async (newOrder) => {
        setHideButton(true)
        const {data: order} = await orderService.create(newOrder);
        reset()

        console.log(fileList)
        for (const file of fileList) {
            formData.set('file', file)
            await orderService.addImage(order._id, formData);
        }

        navigate(`/orders/${order._id}?orderCreated=true`)
    }

    return (
        <div>
            <form onSubmit={handleSubmit(submit)}>
                <div>
                    <select {...register('priority')}>
                        <option value="" disabled selected hidden></option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                    </select>
                    <label> </label>
                </div>
                <div>
                    <select {...register('jobType')}>
                        <option value="" disabled selected hidden> </option>
                        <option value='generalConstructionWorks'> </option>
                        <option value='refrigerationEquipment'> </option>
                        <option value='technologicalEquipment'> </option>
                        <option value='ventilationAndAirConditioning'>   </option>
                        <option value='liftingEquipmentAndElevators'>   </option>
                        <option value='dieselGenerators'> </option>
                        <option value='electricity'></option>
                        <option value='waterAndHeating'>  </option>
                    </select>
                    <label> </label>
                </div>
                <div>
                    <input type="text" {...register('description')}/>
                </div>
                <div>
                    <input type="file" onChange={handleFileEvent} multiple
                           disabled={fileLimit}
                           accept='image/gif, image/jpeg, image/png'
                    />

                    <div>
                         :
                        {fileList.map(file => <p key={file.lastModified}> {file.name}</p>)}
                    </div>
                </div>
                {!hideButton && <button disabled={!isValid}> </button>}
            </form>

        </div>
    );
};

export {CreateOrder};