import {useDispatch, useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {useForm} from "react-hook-form";

import css from './Orders.module.css'
import {orderAction} from "../../../redux";
import {Order} from "../Order/Order";
import {formFilterOrderHelper} from "../../../helpers";

const Orders = () => {

    const dispatch = useDispatch();
    const [filter, setFilter] = useState({});
    const {orders, count, limit, page} = useSelector(state => state.orderReducer);
    useEffect(() => {
        dispatch(orderAction.getAll(filter))
    }, [filter]);

    const {handleSubmit, register, reset, formState: {isValid, errors}} = useForm();

    const submit = (formFilter) => {
        const formFilterHelper = formFilterOrderHelper(formFilter);

        if (Object.keys(formFilterHelper).length !== 0) {
            setFilter(formFilterHelper)
        }
    };
    const resetFilter = () => {
        reset()
        setFilter({})
    };

    const nextPage = () => {
        if (page * limit < count) {
            setFilter({...filter, page: page + 1})
        }
    }
    const prevPage = () => {
        if (page > 1) {
            setFilter({...filter, page: page - 1})
        }
    }

    return (
        <div>
            <form onSubmit={handleSubmit(submit)}>
                <input type="number" placeholder={'page'} {...register('page')}/><label></label><br/>
                <input type="number" placeholder={'limit'} {...register('limit')}/><label></label><br/>
                <div>
                    <select {...register('jobType')}>
                        <option value="" disabled selected hidden> </option>
                        <option value=''></option>
                        <option value='generalConstructionWorks'> </option>
                        <option value='refrigerationEquipment'> </option>
                        <option value='technologicalEquipment'> </option>
                        <option value='ventilationAndAirConditioning'>   </option>
                        <option value='liftingEquipmentAndElevators'>   </option>
                        <option value='dieselGenerators'> </option>
                        <option value='electricity'></option>
                        <option value='waterAndHeating'>  </option>
                    </select>
                    <label> </label>
                </div>

                <div>
                    <select {...register('orderStatus')}>
                        <option value="" disabled selected hidden> </option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=' '> </option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                    </select>
                    <label> </label>
                </div>

                <div>
                    <input type="checkbox" {...register('overdue')}/>
                    <label> </label>
                </div>

                <div>
                    <select {...register('priority')}>
                        <option value="" disabled selected hidden></option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                    </select>
                    <label> </label>
                </div>

                <button>filter</button>
            </form>
            <div>
                <button onClick={resetFilter}>reset filter</button>
            </div>

            <div>
                  {count} <br/>
                  {limit} <br/>
                 {page}
            </div>

            <table>
                <thead>
                <tr>
                    <th> </th>
                    <th> </th>
                    <th></th>
                    <th></th>
                    <th> </th>
                    <th> </th>
                </tr>
                </thead>
                <tbody>
                {orders.map(order => <Order key={order._id} order={order}/>)}
                </tbody>
            </table>

            <div>
                <button onClick={prevPage}></button>
                <button onClick={nextPage}></button>

            </div>

        </div>
    );
};

export {Orders};