import {useForm} from "react-hook-form";
import {joiResolver} from "@hookform/resolvers/joi";
import {useState} from "react";
import {useNavigate} from "react-router-dom";

import {createContractorValidator} from "../../../validators";
import {contractorService} from "../../../services";

const ContractorCreate = () => {

    const navigate = useNavigate();
    const [hideButton, setHideButton] = useState(false);
    const [error, setError] = useState(null);

    const {
        handleSubmit,
        register,
        reset,
        formState: {isValid, errors}
    } = useForm({resolver: joiResolver(createContractorValidator), mode: 'all'});


    const submit = async (newContractor) => {
        try {
            setHideButton(true)
            const {data} = await contractorService.create(newContractor);
            reset()
            setError(null)
            navigate(`/contractors/${data._id}?contractorCreated=true`)
        } catch (e) {
            setError(e.response.data)
            setHideButton(false)
        }

    }

    return (
        <div>
            <form onSubmit={handleSubmit(submit)}>
                <div>
                    <select {...register('region')}>
                        <option value="" disabled selected hidden></option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                    </select>
                    <label>  </label>
                </div>
                <div>
                    <input type="text" placeholder={' '} {...register('name')}/><label>
                    </label>
                    {errors.name && <span>{errors.name.message}</span>}
                </div>
                <div>
                    <input type="text" placeholder={''} {...register('email')}/><label></label>
                    {errors.email && <span>{errors.email.message}</span>}
                </div>
                <div>
                    <input type="text" placeholder={''} {...register('password')}/><label></label>
                    {errors.password && <span>{errors.password.message}</span>}
                </div>
                <div>
                    <input type="text" placeholder={''} {...register('phone')}/><label></label>
                    {errors.phone && <span>{errors.phone.message}</span>}
                </div>
                <div>
                    <input type="text" placeholder={' '} {...register('representative')}/><label>
                    </label>
                    {errors.representative && <span>{errors.representative.message}</span>}
                </div>
                <div>
                    <input type="text" placeholder={''} {...register('jobPosition')}/><label></label>
                    {errors.jobPosition && <span>{errors.jobPosition.message}</span>}
                </div>

                {!hideButton && <button disabled={!isValid}> </button>}
            </form>
            {error && <h1>{error.message}</h1>}
        </div>
    );
};


export {ContractorCreate};