import {useNavigate} from "react-router-dom";
import {useEffect, useState} from "react";
import {useForm} from "react-hook-form";
import {joiResolver} from "@hookform/resolvers/joi";

import {createUserValidator} from "../../../validators";
import {locationService, userService} from "../../../services";

const UserCreate = () => {

    const navigate = useNavigate();
    const [hideButton, setHideButton] = useState(false);
    const [locations, setLocations] = useState([]);
    const [error, setError] = useState(null);

    useEffect(() => {
        locationService.getAbsolutelyAll().then(({data}) => setLocations(data.locations))
    }, [])

    const {
        handleSubmit,
        register,
        reset,
        formState: {isValid, errors}
    } = useForm({resolver: joiResolver(createUserValidator), mode: 'all'});


    const submit = async (newUser) => {
        try {
            setHideButton(true)
            const {data} = await userService.create(newUser);
            reset()

            navigate(`/users/${data._id}?userCreated=true`)
            setError(null)
        }catch (e) {
            setError(e.response.data)
            setHideButton(false)
        }

    }

    return (
        <div>
            <form onSubmit={handleSubmit(submit)}>

                <div>
                    <input type="text" placeholder={'\''} {...register('firstName')}/><label>'</label>
                    {errors.firstName && <span>{errors.firstName.message}</span>}
                </div>
                <div>
                    <input type="text" placeholder={''} {...register('lastName')}/><label></label>
                    {errors.lastName && <span>{errors.lastName.message}</span>}
                </div>
                <div>
                    <input type="text" placeholder={''} {...register('profession')}/><label></label>
                    {errors.profession && <span>{errors.profession.message}</span>}
                </div>
                <div>
                    <input type="text" placeholder={''} {...register('email')}/><label></label>
                    {errors.email && <span>{errors.email.message}</span>}
                </div>
                <div>
                    <input type="text" placeholder={''} {...register('password')}/><label></label>
                    {errors.password && <span>{errors.password.message}</span>}
                </div>
                <div>
                    <input type="text" placeholder={''} {...register('phone')}/><label></label>
                    {errors.phone && <span>{errors.phone.message}</span>}
                </div>
                <div>
                    <select {...register('accessLevel')}>
                        <option value="" disabled selected hidden> </option>
                        <option value='40'></option>
                        <option value='60'></option>
                        <option value='80'> </option>
                        <option value='100'></option>
                    </select>
                    <label> </label>
                </div>
                <div>
                    <select {...register('location')}>
                        <option value="" disabled selected hidden>'</option>
                        {locations.map(location =>
                            <option value={location._id} key={location._id}> {location?.fullAddress} </option>)}
                    </select>
                    <label>'</label>
                </div>

                {!hideButton && <button disabled={!isValid}> </button>}
            </form>
            {error&&<h2>{error.message}</h2>}
        </div>
    );
};

export {UserCreate};